.full-width {
  width: 100% !important;
  box-sizing: border-box;
}

img.full-width {
  height: auto;
}

.max-full-width {
  max-width: 100% !important;
  box-sizing: border-box;
}

.full-height {
  height: 100% !important;
  box-sizing: border-box;
}

img.full-height {
  width: auto;
}

.max-full-height {
  max-height: 100% !important;
  box-sizing: border-box;
}

.clearfix:after {
  content: "";
  display: table;
  clear: both;
}

.margin-reset {
  margin: 0 !important;
}

.padding-reset {
  padding: 0 !important;
}

.rock-slider .rock-box {
  display: inline-block;
  position: relative;
}

.rock-slider .rock-heading {
  padding: 10px 20px;
  display: block;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.5);
  margin: 0;
}

.rock-slider .rock-xlarge {
  font-size: 128px;
}

.rock-slider .rock-large {
  font-size: 72px;
}

.rock-slider .rock-normal {
  font-size: 48px;
}

.rock-slider .rock-small {
  font-size: 32px;
}

.rock-slider .rock-xsmall {
  font-size: 20px;
}

.rock-slider .rock-overlay {
  background: rgba(0, 0, 0, 0.6);
  width: 100%;
  height: 100%;
  position: absolute;
  display: block;
  z-index: 4;
}

.rock-slider .rock-separator {
  display: inline-block;
  width: 60px;
  height: 1px;
  margin: 25px auto;
}

.rock-slider .rock-spacer {
  display: block;
  margin: 20px 0;
}

.rock-slider .rock-spacer-2x {
  margin: 40px 0;
}

.rock-slider .rock-dots {
  margin: -30px 0 10px;
}

.rock-slider .rock-inline-block {
  display: inline-block !important;
}

.rock-slider .rock-square-background-white {
  padding: 20px 60px;
  background: #fff;
  color: #111 !important;
}

.rock-slider .rock-square-background-black {
  padding: 20px 60px;
  background: #111;
  color: #fff !important;
}

.rock-slider .rock-square-border {
  border: 1px solid #fff;
  padding: 20px 60px;
  transition: color 0.5s ease, background 0.5s ease;
}

.rock-slider .rock-square-border.rock-square-border-hover:hover {
  background: #fff;
  color: #111 !important;
}

.rock-slider .rock-square-border.rock-square-border-black {
  border-color: #111;
}

.rock-slider .rock-square-border.rock-square-border-black.rock-square-border-hover:hover {
  background: #111;
  color: #fff !important;
}

.rock-slider .rock-square-border-vertical {
  border-top: 1px solid #fff;
  border-bottom: 1px solid #fff;
  padding: 20px 60px;
  transition: color 0.5s ease, background 0.5s ease;
}

.rock-slider .rock-square-border-vertical.rock-square-border-hover:hover {
  background: #fff;
  color: #111 !important;
}

.rock-slider .rock-square-border-vertical.rock-square-border-black {
  border-color: #111;
}

.rock-slider .rock-square-border-vertical.rock-square-border-black.rock-square-border-hover:hover {
  background: #111;
  color: #fff !important;
}

.rock-slider .rock-white-transparent {
  background: rgba(255, 255, 255, 0.75);
}

.rock-slider .rock-black-transparent {
  background: rgba(17, 17, 17, 0.75);
}

.rock-slider .rock-highlight {
  padding: 20px 20px;
  display: block;
}

.rock-slider .rock-word-cloud-background {
  background: #fff;
  position: absolute;
  width: 100%;
  height: 120px;
  margin: 0 -15px;
  display: block;
  top: 195px;
}

.rock-slider .rock-word-cloud {
  display: block;
}

.rock-slider .rock-word-cloud .rock-word-cloud-item {
  white-space: nowrap;
  width: 100%;
  text-align: center;
  font-size: 72px;
  color: #fff;
  font-weight: bold;
}

.rock-slider .rock-margin-top {
  margin-top: 50px;
}

.rock-slider .rock-margin-bottom {
  margin-bottom: 50px;
}
